@import "mixins.scss";
@import "constants.scss";

.productItem {
	box-sizing: border-box;
	display: inline-block;
	width: 300px;
	height: 425px;
	background: $main-color;
	padding: 30px 44px;
	margin: 10px;

	.productColumn {
		@include column(365px, space-between, center);
		text-align: center;

		.productPhoto {
			img {
				width: 177px;
				height: 233px;
			}
		}

		.productName {
			font-size: 18px;
		}

		.productPrice {
			font-size: 24px;
		}
	}

	transition: 1s;

	border: 1px solid $main-color;

	&:hover {
		// width: 299px;
		// height: 420px;
		transition: 1s;
		border: 1px solid white;
	}
}

.hideElem {
	display: none;
}

@media only screen and (max-width : 500px) {
	.productItem {
		width: 100%;
		margin: 10px 15px;

	}
}